<template>
  <div class="login" style="background:#fff ">
    <div class="topBgcolor"></div>
    
    <!--登录页面 -->
    <div class="login-box">
      <!-- 顶部导航 -->
      <van-nav-bar>
        <template #left>
          <div class="left-text">
            <div class="logo">
              <img class="auto-img" src="../assets/images/home_active.png" alt="">
            </div>
            <div class="logo-title">Luckin Coffce</div>
          </div>
        </template>
        <template #right>
          <div class="right-text">
            <div @click="goHome()">去首页</div>
          </div>
        </template>
      </van-nav-bar>

      <!--  -->
      <div class="login-form">
        <div class="title">欢迎回来!</div>
        <div class="en-title">Please login to your accounts</div>

        <!-- 表单 -->
        <div class="login-form-box">
          <van-form>
            <van-field
              class="border-bottom"
              label="手机号："
              placeholder="手机号"
              v-model="userLoginInfo.phone"
            />
            <van-field
              class="border-bottom"
              :type="isPassword ? 'password' : 'text'"
              label="密码："
              placeholder="密码"
              v-model="userLoginInfo.password"
              :right-icon="isPassword ? 'closed-eye' : 'eye-o'"
              @click-right-icon="viewPassword"
            />
            <div class="clearfix forgot" @click="forgot">
              <span class="fr">忘记密码?</span>
            </div>
            <div class="login-btn">
              <van-button
                block
                type="info"
                native-type="submit"
                @click="login"
              >登&nbsp;&nbsp;录</van-button>
            </div>
            <div class="login-btn register-box">
              <!-- 注册弹出层 -->
              <van-cell
                class="register"
                @click="showPopup"
              >注&nbsp;&nbsp;册</van-cell>
              <van-popup
                class="register-message"
                :style="{ height: '60%' }"
                position="bottom"
                round
                closeable
                v-model="show"
              >
                <h2>注册</h2>

                <!-- 注册表单 -->
                <div class="message-box">
                  <van-form>
                    <van-field
                      class="border-bottom"
                      placeholder="11位手机号"
                      v-model="userregisterInfo.phone"
                    />
                    <van-field
                      class="border-bottom"
                      placeholder="1-10位昵称"
                      v-model="userregisterInfo.nickName"
                    />
                    <van-field
                      class="border-bottom"
                      :type="ispassword ? 'password' : 'text'"
                      placeholder="6-16位密码且以字母开头"
                      v-model="userregisterInfo.password"
                      :right-icon="ispassword ? 'closed-eye' : 'eye-o'"
                      @click-right-icon="viewpassword"
                    />
                    <div class="register-btn">
                      <van-button
                        type="info"
                        @click="register"
                        native-type="submit"
                      >注&nbsp;&nbsp;册</van-button>
                    </div>
                  </van-form>
                </div>
              </van-popup>
              <!-- <van-button  block type="default">注&nbsp;&nbsp;册</van-button> -->
            </div>
          </van-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/Login.css";
import { validForm } from "../assets/js/validForm";
import { utils } from "../assets/js/utils";
// import { Col } from "vant";

export default {
  data() {
    return {
      userLoginInfo: {
        phone: "",
        password: "",
      },
      userregisterInfo: {
        phone: "",
        nickName: "",
        password: "",
      },
      isPassword: true,
      ispassword: true,
      show: false,
    };
  },
  methods: {
    goHome() {
      this.$router.push({name:'Home'})
    },

    // 登录查看密码
    viewPassword() {
      
      this.isPassword = !this.isPassword;
    },

    // 注册查看密码
    viewpassword() {
      
      this.ispassword = !this.ispassword;
    },

    // 注册 弹出层
    showPopup() {
      this.show = true;
      this.userregisterInfo.phone = "";
      this.userregisterInfo.nickName = "";
      this.userregisterInfo.password = "";
    },

    // 注册
    register() {
      // 获取用户注册信息
      let userInfo = this.userregisterInfo;
      

      //   验证注册表单
      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号码格式不正确",
        },
        nickName: {
          value: userInfo.nickName,
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg: "昵称格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };

      if (!validForm.valid(o)) {
        //   如果验证不通过
        return;
      }

      //   发起请求
      // 开启加载提示
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 图标
        loadingType: "spinner",
        // 显示时间，如果时间位0，则不会自动关闭
        duration: 0,
      });

      //   序列化参数
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      let data = utils.queryString(info);
      

      this.axios({
        method: "POST",
        //   请求参数
        url: "/register",
        data,
      })
        .then((result) => {
          //   关闭加载提示;
          this.$toast.clear();
          
          if (result.data.code == 100) {
            this.show = false;
            for (let key in userInfo) {
              userInfo[key] = "";
            }
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 登录
    login() {
      let userInfo = this.userLoginInfo;

      let o = {
        phone: {
          value: userInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号码格式不正确",
        },
        password: {
          value: userInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码格式不正确",
        },
      };

      // 验证表单
      if (!validForm.valid(o)) {
        return;
      }

      // 验证通过
      // 开启加载提示
      this.$toast.loading({
        message: "加载中...",
        // 防止穿透
        forbidClick: true,
        // 图标
        loadingType: "spinner",
        // 显示时间，如果时间位0，则不会自动关闭
        duration: 0,
      });

      // 复制userInfo
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;
      //   序列化参数
      let data = utils.queryString(info);
      

      this.axios({
        method: "POST",
        //   请求参数
        url: "/login",
        data,
      })
        .then((result) => {
          //   关闭加载提示;
          this.$toast.clear();
          
          if (result.data.code == 200) {
            localStorage.setItem('CSTK',result.data.token);
            this.$router.push({name:'Home'})
          } else {
            this.$notify({
              type: "warning",
              message: result.data.msg,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 忘记密码
    forgot(){
      this.$router.push({name:'Forgot'});
    }

  },
};
</script>

<style lang="less" scoped>
</style>